.about-me{
    padding: 30px 0;
}

.about-me h2{
    font-size: 60px;
    color: rgb(180, 181, 182);
    font-family: 'PT Serif', serif;
}

.myImage{
    padding-top: 70px;
}

.about-me img {
    height: 100%;
    max-height: 280px;
    width: 70%;
}

.about-me ul{
    list-style-type: none;
    margin-top: 10px;
    padding: 0;
}

.about-me ul li{
    display: inline;
    margin-left: 20px;
}

.about-me ul li :hover img{
    -webkit-transform: scale(1.3, 1.2);
            transform: scale(1.3, 1.2);
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.about-me ul li img{
    height: 50px;
    width: 50px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.my-details{
    text-align: left;
}

.my-details p {
    color: rgb(112, 113, 114);
    font-family: 'Source Sans Pro', sans-serif;
}

p span{
    font-weight: bold;
}

.about-me .btn{
    font-size: 18px;
    border-radius: 20px;
    font-family: 'Lora', serif;
}


