@media (min-width: 1200px) {
    
}

@media (max-width: 767.98px) {
    .about-me .btn-primary{
        margin-bottom: 20px;
    }
}
.skills{
    background: #0A0A0A;
    color: #E9E9E9;
    padding: 30px 0;
}

.skills h2{
    margin-bottom: 50PX;
    font-family: 'PT Serif', serif;
    font-size: 50px;
}

.skills h3{
    font-family: 'PT Serif', serif;
    font-size: 30PX;
}

.skills .languages,
.skills .concepts,
.skills .technologies{
    text-align: left;
    border-left: 1px solid red;
}

.skills li{
    font-family: 'Lora', serif;
}
.portfolio{
    padding: 50px 0;
}

.portfolio .card{
    margin-top: 20px;
    height: 300px;
}

.portfolio .card-title{
    font-weight: bolder;
    font-family: 'PT Serif', serif;
    margin-top: 25%;
}

.portfolio .card .card-text{
    font-size: 17px;
    margin-bottom: 5px;
    font-family: 'Lora', serif;
    padding: 0 20px;
}

.card-img-top{
    height: 300px;
}

.portfolio .card-overlay{
    text-align: center;
    color: white;
    background-color: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.portfolio .card:hover .card-overlay{
    display: block;
}
.portfolio h2{
    font-size: 50px;
    font-family: 'PT Serif', serif;
    margin-bottom: 30px;
}
.contact-me{
    background: #0A0A0A;
    color: white;
    min-height: 50px;
    padding: 15px 0;
}

.contact-me p span img {
    height: 20px;
    width: 20px;
    margin-right: 5px;
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .contact-me .container{
        max-width: 990px !important;
    }
}
